.featured-games {
    flex: 1 65%;
    background-color: var(--color-darkest);
    border-radius: var(--border-primary);
    max-height: 10%;
}
.top-features-items { 
    margin-right: 3%;
    display: flex;
    flex-wrap: nowrap;
    padding-left: 5%;
    padding-right: 5%;
    padding-top: 1%;
    padding-bottom: 2%;
    background-color: var(--color-darkest);
    border-radius: var(--border-primary);
    overflow: hidden;
}
.featured-games-text {
    margin-left: 5%;
    margin-top: 5%;
}
.featured-games-text h4 {
    font-size: 35px;
    font-weight: 700;
}
.featured-games-text h4 span {
    color: var(--color-primary);
}
@media only screen and (max-width: 1000px) {
    .featured-games {
        display: block;
    }
    .top-features-items {
        display: block;
    }
    
    
}