.datsec-c h6 {
    font-size: 18px;
    font-weight: 700;
    line-height: 40px;
}
.datsec-c h7 {
    color: var(--color-lighter);
}
.datsec-c ul li {
    font-size: 14px;
    font-weight: 700;
}
.datsec-c ul {
    display: grid;
    padding-top: 5px;
    grid: 20px;
    gap: 10px;
    justify-item: center;
    height: 100%;
}


