.your-most-popular-card {
    background-color: var(--color-dark);
    border-radius: var(--border-primary);
    padding: 10px;
    flex: 1 23%;
    margin-right: 15px;
    margin-bottom: 15px;
    padding-top: 3%;
}
.your-most-popular-card .img {
    padding: 0;
    margin: 0;
}
.your-most-popular-card .img img {
    border-radius: var(--border-primary);
    margin: auto;
    width: 90%;
}
.your-most-popular-card div {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 4%;
    padding-left: 10px;
    padding-right: 10px;
}
.your-most-popular-card div h4 {
    font-size: 15px;
    font-weight: 300;
}
.your-most-popular-card div h5 {
    font-size: 13px;
    font-weight: 300;
    color: whitesmoke;
}