.top-wrappr {
    display: flex;
    flex-wrap: wrap;
    align-items: start;
}
.top-featured {
    flex: 1 30%;
    background-color: var(--color-darkest);
    margin-left: 10px;
    border-radius: var(--border-primary);
    padding: 15px;
    max-height: fit-content;
}
.top-featured div h4 {
    font-weight: 700;
    margin-top: 10px;
}
.top-featured div h4 span {
    color: var(--color-primary);
}
.top-featured-btn {
    margin-top: 5%;
    text-align: center;
    padding-bottom: 3%;
}

@media only screen and (max-width: 1000px) {
    .top-featured > div {
        text-align: center;
    }
    .top-featured {
        margin-top: 10px;
    }
}