.top-featured-cont {
    display: flex;
    flex-wrap: wrap;
    margin-top: 10%;
}
.top-featured-cont:hover {
    background-color: var(--color-dark);
    border-radius: var(--border-primary);
    cursor: pointer;
    padding: 7px;
}
.top-featured-cont div {
    flex: 25%;
}
.top-featured-cont div img {
    border-radius: var(--border-primary);
    max-width: 85%;
}
.top-featured-text {
    display: flex;
    padding-right: 15px;
}

.top-featured-text h4 {
    font-weight: 700;
    font-size: 15px;
}
.top-featured-text h4 span {
    font-weight: 300;
    color: aliceblue;
}
.top-featured-icon {
    margin-left: 2px;
    border-radius: 50%;
    background-color: var(--color-dark);
    max-width: 50px;
    max-height: 50px;
}
.top-featured-icon div 
{
    margin-left: 33%;
    
    margin-top: 20%;
}
.top-featured-line {
    border-bottom: 1px solid var(--color-dark);
    margin-top: 3%;
    
    margin-left: 3%;
}
@media only screen and (max-width: 1000px) {

    .top-featured-text {
        display: flex;
        justify-content: space-between;
    }
    .top-featured-line {
        width: 100%;
        border: 0;
    }
    .top-featured-icon>div {
        margin: 0;
        height: 50px;
        text-align: center;
    }
    .top-featured-icon {
        margin-bottom: 10px;
        width: 100%;
        text-align: center;
        margin-left: 30%;
        margin-top: 10px;
    }
    .top-featured-cont {
        border-bottom: 1px solid var(--color-dark);

    }
    .top-featured-cont:hover {
        border-bottom: 0px;
    }
}