body {

    max-width: 99%;
}
.profile-form {
    display: flex;
    flex-wrap: wrap;
    border-bottom: 1px solid var(--color-dark);
    padding-bottom: 5%;
}
.profile-form img {
    border-radius: var(--border-primary);
    width: 30%;
}
.profile-texts {
    padding: 3%;
    display: block;
}
.profile-texts h2 {
    
    margin-top: 3%;
}
.profile-texts h4 {
    font-size: 18px;
    font-weight: 300;
    margin-bottom: 5%;
    color: var(--color-lighter);
    
}
.profile-info {
    display: block;
    border-radius: var(--border-primary);
    background-color: var(--color-dark);
    padding: 20px;
    width: 30%;
}
.profile-info div {
    margin-top: 5px;
    padding-bottom: 10px;
    padding-top: 10px;
    border-bottom: 1px solid var(--color-darkest);
    width: 100%;
    justify-content: space-between;
    display: flex;
    flex-wrap: wrap;
}
.profile-info div h6 {
    font-size: 15px;
}
.profile-info div h6 em {
    font-size: 13px;
    color: whitesmoke;
}
.s-btn-pf {
    padding-top: 10%;
    padding-left: 10%;
}
@media only screen and (max-width: 1000px) {
    .profile-form {
       display: block; 
    }
    .profile-form img {
        width: 100%;
        padding: 10px;
     }
     .profile-texts {
        text-align: center;
     }
     .profile-texts >h2{
        margin-top: 15px;
     } 
     .profile-info {
        margin-top: 15px;
        width: 100%;
     }
}