.top-wrappr {
    min-width: 100px;
    width: 100%;
}
.main-dev-stream-title {
    margin-top: 5%;
}
.main-dev-stream-title h4{
    font-weight: 700;
    font-size: 35px;
    text-align: center;
}
.main-dev-stream-title h4 span {
    color: var(--color-primary);
}
.main-dev-stream-con {
    margin-top: 5%;
    display: inline-flex;
}
.f-con {
    border: 1px solid var(--color-lighter);
    margin-left: 3%;
    padding: 18px;
    border-radius: var(--border-primary);
}
.f-con img {
    padding: 10px;
    border-radius: 50%;
}
.f-con p {
    color:var(--color-lighter);
    font-weight: 300;
    font-size: 14px;
    line-height: 27px;
}
.str-btn {
    text-align: center;
    margin-top: 5%;
}
@media only screen and (max-width: 1000px) {
    .main-dev-stream-con { 
        display: block;
    }
    .f-con { 
        margin-bottom: 10px;
    }
}