.main-button {
    width: 100%;
}
.main-button a {
    font-size: 14px;
    border-radius: var(--border-primary);
    padding: 12px 30px;
    letter-spacing: 0.5px;
    width: 100%;
    transition: 0.3 all;
}
.primary-btn a {
    background-color: var(--color-primary);  
    color: var(--color-light); 
}
.primary-btn a:hover {
    background-color: var(--color-light);
    color: var(--color-primary);
}
.secondry-btn {
    color:  var(--color-light);
}
.secondry-btn a {
    border: 1px solid var(--color-primary);
    width: 100%;
}
.secondry-btn a:hover {
    background-color: var(--color-light);
    
}
.secondry-btn:hover {
    color: var(--color-primary);
}
.therd-btn{
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-content:center;
    width: 100%;
    border: 1px solid var(--color-light);
    height: 40px;
    border-radius: var(--border-primary);
}
.therd-btn a {
    margin-top: 6px;
}
.therd-btn:hover  {
    color: var(--color-primary);
    background-color: var(--color-light);
    
}

