.gaming-library-card {
    flex: 1 1 100%;
}
.gaming-library-card ul {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid var(--color-primary) ;
    padding: calc(var(--padding-primary)/ 2) 0;
}
.gaming-library-card ul li img {
    border-radius: calc(var(--border-primary)/2) ;
    width: 70%;
    margin-left: 5px;
    
}
.gaming-library-card h4 {
    font-size: 1rem;
    margin-top: 20%;
}
.gaming-library-card > ul > li > span{
    color: var(--color-lighter);
    
}
.btn-s {
    margin-right: 10px;
    padding-top: 3%;
    
}
.gaming-library-card ul:hover {
    background-color: var(--color-dark);
    border-radius: calc(var(--border-primary)/2) ;
    border-bottom: 1px solid var(--color-dark) ;
}
@media only screen and (max-width: 1000px) {
    .gaming-library-card ul{ 
        display: block;
        text-align: center;
        
    }
    .gaming-library-card h4 {
        margin: 10px;
    }
}
