.main-str {
    
    margin-left: 5px;
    text-align: center;
    
}
.str-img img {
    border-radius: var(--border-primary);
    
    max-width: 200px;
}
.str-info {
    display: inline-flex;
}
.str-info div img { 
    border-radius: 50%;
    margin-top: 35%;
}
.str-text h5{
    font-size: 15px;
    font-weight: 300;
    color: var(--color-primary);
    margin-left: 5px;
}
.str-texts h4{
    font-size: 20px;
    font-weight: 700;
    padding-bottom: 15px;
}

@media only screen and (max-width: 1000px) { 
    .main-str  {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        padding: 0;
        margin-bottom: 10px;
    }
    .str-img {
        margin-right: 10px;
        flex: 50%;
    }
    .str-img > img {
        width: 100%;
    }
    .str-info { 
        width: 50%;
        
    }
    .str-info {
        display: flex;
    }
    .str-info > div {  
        height: 50px;
        flex: 50%;
    }
    .str-info > div > img {
        margin-top: 60%;
    }
    .str-texts > h4 {
        margin-top: 10px;
        line-height: 23px;
        font-size: 14px;
    }
}