.other-related-games h2 {
    font-weight: 700;
    font-size: 35px;
}
.other-related-games h2 span {
    color: var(--color-primary)
}

.org-c {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
}
.org-i {
    display: flex;
    width: 45%;
    margin-bottom: 20px;
    margin-top: 20px;
    margin-right: 30px;
    border-bottom: 1px solid var(--color-lighter);
    padding-bottom: 40px;
}
.org-i> img {
    width: 95px;
    border-radius: var(--border-primary);
}
.org-t {
    margin: 10px;
    display:flex;
    justify-content:space-between;
    width: 100%;
} 
.org-h {
    padding-top: 10px;
}
.org-h h6{
    font-weight: 700;
    font-size: 15px;  
}
.org-h p{
    color: var(--color-lighter);
}
.org-t ul {
    display: grid;
    padding-top: 10px;
}
.org-line {
    border: 1px solid #fff;
}
@media only screen and (max-width: 1000px) {
    .org-c {
        display: block;
        
    }
    .org-t {
        display: flex;
        justify-content: space-between;
    }
    .org-i {
        width: 100%;
    }
    .org-h h6{
        font-size: 13px;  
    }
    .org-i> img {
        max-height:  95px;
    }
    .org-t ul {
        display: grid;
        padding-top: 5px;
        font-size: 13px;
        height: 100%;
        padding-left: 0px;
    }

}