.item-f {
    flex: 10%;
    margin-left: 3px;
}
.thumb {
    padding: 5px;
}
.thumb> img {
    border-radius: var(--border-primary);
}
.hoover-effect {
    display: none;
    padding: 5px;
    border-radius: var(--border-primary);
    color: var(--color-light);
    background-color: var(--color-primary);
}
.hoover-effect h6 {
    font-size: 17px;
    font-weight: 700; 
    margin: auto;  
}
.item-t {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-left: 3%;
    padding-top: 5%;
}
.item-t h4 {
    font-size: 14px;
    font-weight: 700;
    flex: 1 50%;
    line-height: 22px;
}
.item-t h4 span {
    font-size: 12px;
    font-weight: 300;
    flex: 1 50%;
    color: aliceblue;
}
.item-t ul {
    font-size: 14px;
    font-weight: 300;
    flex: 1 50%;
}
@media only screen and (max-width: 1000px) {
    .thumb> img{
        width: 100%;
    }
    .item-f:nth-child(2),.item-f:nth-child(1) {
        display: none;
    }
}
