.hero-main {
    background: url('../../assets/images/banner-bg.jpg') no-repeat top right/cover;
    background-color: var(--color-dark);
    color: var(--color-light);
    border-radius: var(--border-primary);
    padding: var(--padding-primary);
}
.hero-text {
    max-width: 500px;
}
.hero-title {
    font-size: 60px;
    text-transform: uppercase;
    margin-bottom: var(--margin-primary);
    font-weight: var(--font-whight-bold);
}

.hero-title em {
    font-style: normal;
    color: var(--color-primary);
}
hero-subtitle {
    font-size: 20px;
    color: white;
    font-weight: var( --font-whight-normal);
}