.detSec-fc {
    display: flex;
    flex-wrap: wrap;
}
.datsec-c {
    background-color: var(--color-dark);
    border-radius: var(--border-primary);
    padding: 20px;
    flex: 1 40%;
    display: flex;
    flex-wrap: nowrap;
    margin-left: 10px;
    margin-right: 10px;
    justify-content: space-between;
    
}

.te {
    justify-content: center;
}
.detSec-sc {
    margin-top: 3%;
}

.detSec-sc p {
    color: var(--color-lighter);
    font-size: 15px;
    line-height: 35px;
}
.detSec-sc p span {
    color: var(--color-light);
}
.detSec-tc {
    display: flex;
    flex-direction: row;
    padding: 10px;
    flex-wrap: nowrap;
    overflow: hidden;
    justify-content: space-between;
}
.detSec-tc > img {
    flex-basis: auto;
    flex-shrink: 20%;
    margin: 10px;
    width: 31%;
    border-radius: var(--border-primary);
}
@media only screen and (max-width: 1000px) {
    .detSec-fc {
        display: block;
        
    }
    .detSec-fc {
        margin-top: 10px;
    }

    .detSec-tc {
        display: block; 
    }
    .detSec-tc> img {
        width: 100%;
    }
}