.det-first- {
    display: flex;
    align-items:start;
    padding-bottom: 20px;
}
.det-first-> img {
    margin: 10px;
    border-radius: var(--border-primary);
    width: 30%;
}
.det-first-link {
    width: 70%;
   position: relative;
}
.det-first-link img {
    width: 100%;
    border-radius: var(--border-primary);
    margin-top: 10px;
}
.det-first-link a {
    position: absolute;
    bottom: 40%;
    left: 45%;
    padding: 5px;
    background-color: var(--color-light);
    height: 50px;
    font-size: 40px;
    border-radius: 50%;
    justify-content: center;
    cursor: pointer;
    opacity: 0.7;
}
.det-first-link a:hover {
    opacity: 1
}
.det-first-link {
    margin-right: 10px;
}
.det-first-text {
    justify-content: center;
}
.det-first-text h4 {
    text-align: center;
    font-size: 50px;
    font-weight: 700;
}
@media only screen and (max-width: 900px) {
    .det-first- {
        display: block;
        text-align: center;
    }
    .det-first-> img {
        width: 100%;
    }
    .det-first-link {
        width: 100%;
    }
    .det-first-link a {
        bottom: 35%;
        left: 40%;
    }
}
