:root {
    --color-dark: #27292a;
    --color-darkest: #1f2122;
    --color-light: #fff;
    --color-primary: #ec6090;
    --color-lighter:  rgb(137, 142, 146);
    --color-bg-body: var(--color-darkest);
    --distance-primary: 50px;
    --padding-primary: var(--distance-primary);
    --margin-primary: calc(var(--distance-primary) / 2);
    --border-primary: calc( var(--distance-primary)/2) ;
    --font-whight-bold: 700;
    --font-whight-normal: 400;
    --font-family-primary: 'Poppins', sans-serif;
}
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@700&family=Poppins:wght@400;700&display=swap');
body {
    background-color: var(--color-bg-body);
    font-family: var(--font-family-primary);
}
ul {
    list-style: none;
}
a {
    text-decoration: none;
}